.offers {
	background-color: var(--bg-pr);
}
.offers .container {
	display: flex;
	align-items: flex-start;
	justify-content: center;
	flex-wrap: wrap;
    gap: 30px;
}
.offers .container .plan {
	background-color: var(--bg-sc);
    text-align: center;
	color: var(--text-color);
	z-index: 0;
}
.offers .container .plan .head {
    padding: 40px 20px;
    border-top: 1px solid var(--main-color);
    border-bottom: 1px solid var(--main-color);
	font-family: 'Josefin Sans', sans-serif;
	direction: ltr;
}
.offers .container .plan .head h3{
    font-weight: normal;
    margin-bottom: 20px;
    text-transform: uppercase;
	font-weight: 600;
}
.offers .container .plan .head span {
    font-size: 60px;
    font-weight: bold;
}
.offers .container .plan .head span::before {
    content: ">=";
    position: relative;
    font-size: 25px;
    top: -40px;
    margin-right: 15px;
    font-weight: normal;
}
.offers .container .plan .head span::after {
    content: "$";
    font-size: 20px;
    position: relative;
    right: -15px;
}
.offers .container .plan ul {
    border-bottom: 1px solid var(--main-color);
}
.offers .container .plan ul li{
    padding: 20px;
    position: relative;
}
.offers .container .plan ul li:not(:last-child)::after{
    content: "";
    width: 120px;
    height: 1px;
    background-color: var(--main-color);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
}
.offers .container .plan ul li span:nth-child(1){
    text-transform: capitalize;
	font-size: 17px;
	font-weight: 500;
}
.offers .container .plan ul li span:nth-child(2){
    background: var(--main-color);
    color: #fff;
	font-family: 'Josefin Sans', sans-serif;
    padding: 5px;
    border-radius: 4px;
}
.offers .container .plan .foot a {
	display: block;
    padding: 10px 20px;
    text-decoration: none;
    border: 1px solid var(--main-color);
    width: fit-content;
    width: fit-content;
    margin: 30px auto;
    color: var(--main-color);
	position: relative;
	transition: var(--main-transition);
	z-index: 10;
}
.offers .container .plan .foot a:hover {
	color: #fff;
}
.offers .container .plan .foot a::before {
	content: '';
	position: absolute;
	width: 0;
	height: 100%;
	left: 0;
	top: 0;
	background-color: var(--main-color);
	transition: var(--main-transition);
	z-index: -1;
}
.offers .container .plan .foot a:hover::before {
	width: 100%;
}